import { ReactElement } from 'react';

import { Button, Menu, MenuItem, Popover } from '@blueprintjs/core';
import { signOut as firebaseSignOut, getAuth } from 'firebase/auth';

import { AuthState, useAuth, useFirebase } from './AuthenticationHooks';

export default function LoggedInUserButton(): ReactElement {
  const firebase = useFirebase();
  const { authState, userProfile, signOut } = useAuth();

  if (authState !== AuthState.AUTHENTICATED) {
    return <div />;
  }

  const logout = () => {
    firebaseSignOut(getAuth(firebase.app))
      .then((result) => {
        console.log('Sign Out Success', result);
        if (signOut) {
          signOut();
        }
      })
      .catch((result) => {
        console.log('Sign Out Failed', result);
      });
  };

  const userImage = (
    <img
      className="rounded"
      style={{ width: '25px', height: '25px' }}
      src={userProfile?.profileImageUrl}
      alt=""
    />
  );

  const userMenu = (
    <Menu>
      <MenuItem icon="log-out" onClick={logout} text="Log Out" />
    </Menu>
  );

  return (
    <Popover content={userMenu} interactionKind="click" placement="bottom-end" minimal={true}>
      <Button
        style={{ margin: 0 }}
        className="bp3-minimal"
        icon={userImage}
        text={userProfile?.displayName}
      />
    </Popover>
  );
}
