import { useContext, createContext, ReactElement, ReactNode, useState, useEffect } from 'react';
import { initializeApp, FirebaseApp } from 'firebase/app';

export enum AuthState {
  INITIALIZING,
  AUTHENTICATED,
  NOT_AUTHENTICATED
}

export interface UserProfile {
  displayName: string;
  email: string;
  profileImageUrl: string;
}

export type FirebaseAppContextVariables = {
  app?: FirebaseApp;
};

export type AuthContextVariables = {
  authState: AuthState;
  userProfile?: UserProfile;
  signIn?: (userProfile: UserProfile) => void;
  signOut?: () => void;
};

const firebaseAppContext = createContext<FirebaseAppContextVariables>({});
const authContext = createContext<AuthContextVariables>({ authState: AuthState.INITIALIZING });

export function useFirebase(): FirebaseAppContextVariables {
  return useContext(firebaseAppContext);
}

export function useAuth(): AuthContextVariables {
  return useContext(authContext);
}

export type FirebaseAppProviderProperties = {
  apiKey: string;
  projectId: string;
  children: ReactNode | ReactNode[];
};

export type AuthProviderProperties = {
  children: ReactNode | ReactNode[];
};

export function FirebaseAppProvider({
  apiKey,
  projectId,
  children
}: FirebaseAppProviderProperties): ReactElement {
  const value = {
    app: initializeApp({
      apiKey,
      authDomain: `${projectId}.firebaseapp.com`,
      projectId
    })
  };

  return <firebaseAppContext.Provider value={value}>{children}</firebaseAppContext.Provider>;
}

export function AuthProvider({ children }: AuthProviderProperties): ReactElement {
  const auth = createAuth();

  return <authContext.Provider value={auth}>{children}</authContext.Provider>;
}

function createAuth(): AuthContextVariables {
  const [authState, setAuthState] = useState(AuthState.INITIALIZING);
  const [userProfile, setUserProfile] = useState({} as UserProfile);

  useEffect(() => {
    // TODO: Load token from local storage to check if authenticated or not authenticated

    if (authState == AuthState.INITIALIZING) {
      setAuthState(AuthState.NOT_AUTHENTICATED);
    }
  }, [authState]);

  const signIn = (userProfile: UserProfile) => {
    setUserProfile(userProfile);
    setAuthState(AuthState.AUTHENTICATED);
  };

  const signOut = () => {
    setUserProfile({} as UserProfile);
    setAuthState(AuthState.NOT_AUTHENTICATED);
  };

  // TODO: Handle this as well
  // getAuth(getApp()).onAuthStateChanged(function(user) {
  //   if (user) {
  //     // User is signed in. - localStorage.setItem('myPage.expectSignIn', '1')
  //   } else {
  //     // No user is signed in. - localStorage.removeItem('myPage.expectSignIn')
  //   }
  // });

  return { authState, userProfile, signIn, signOut };
}
