import React, { ReactElement, useEffect, useState } from 'react';

import { Card, Elevation } from '@blueprintjs/core';

import { Console, Hook, Unhook } from 'console-feed';
import { Message } from 'console-feed/lib/definitions/Component';

export default function DebugLogs(): ReactElement {
  const [logs, setLogs] = useState([] as Message[]);
  const [runningLocally, setRunningLocally] = useState(false);

  useEffect(() => {
    const value =
      window.location.hostname === 'localhost' ||
      window.location.hostname === '127.0.0.1' ||
      new URL(window.location.href).searchParams.get('debug') === 'true';

    setRunningLocally(value);
  }, [window.location.hostname, window.location.href]);

  useEffect(() => {
    if (runningLocally) {
      const hook = Hook(
        window.console,
        (log) => setLogs((currentLogs) => [...currentLogs, log as Message]),
        false
      );

      console.log('Initialized Debug Logs');

      return () => {
        Unhook(hook);
      };
    }
  }, [runningLocally]);

  if (runningLocally) {
    return (
      <div className="debugLogsCardContainer">
        <Card elevation={Elevation.TWO}>
          <h2>Debug Logs</h2>
          <Console logs={logs} />
        </Card>
      </div>
    );
  }

  return <React.Fragment />;
}
