import './App.css';

import { ReactElement } from 'react';
import { Outlet, useNavigate } from 'react-router-dom';

import { Button, Navbar, IconName, Alignment } from '@blueprintjs/core';

import { AuthState, useAuth } from './features/authentication/AuthenticationHooks';

import UserMenuButton from './features/authentication/UserMenuButton';
import LoginCard from './features/authentication/LoginCard';
import DebugLogs from './features/debug/DebugLogs';

export default function App(): ReactElement {
  const { authState } = useAuth();
  const navigate = useNavigate();

  function createButton(name: string, icon: IconName, target: string) {
    return (
      <Button
        key={name}
        className="bp3-minimal"
        icon={icon}
        text={name}
        disabled={authState !== AuthState.AUTHENTICATED}
        onClick={() => navigate(target)}
      />
    );
  }

  const navigationButtons = [
    createButton('Overview', 'home', '/'),
    createButton('Data', 'database', '/data'),
    createButton('Planning', 'chart', '/planning'),
    createButton('Review', 'endorsed', '/review')
  ];

  return (
    <div className="App">
      <Navbar className="bp3-dark">
        <Navbar.Group>
          <Navbar.Heading>Personal Finance</Navbar.Heading>
          <Navbar.Divider />
          {navigationButtons}
        </Navbar.Group>

        <Navbar.Group align={Alignment.RIGHT}>
          <UserMenuButton />
        </Navbar.Group>
      </Navbar>

      <div style={{ padding: '0 50px' }}>
        {authState === AuthState.AUTHENTICATED ? <Outlet /> : <LoginCard />}

        <div style={{ margin: '50px 0px' }}>
          <DebugLogs />
        </div>
      </div>
    </div>
  );
}
