import { createRoot } from 'react-dom/client';

import { Provider } from 'react-redux';
import { BrowserRouter } from 'react-router-dom';

import { HotkeysProvider } from '@blueprintjs/core';

import './index.css';

import { store } from './app/Store';
import Routes from './Routes';
import { AuthProvider, FirebaseAppProvider } from './features/authentication/AuthenticationHooks';

const baseUri = new URL(document.baseURI);
const metaTags = document.head.getElementsByTagName('meta');

const firebaseApiKey = metaTags.namedItem('firebase-apiKey')?.content || '';
const firebaseProjectId = metaTags.namedItem('firebase-projectId')?.content || '';

const rootNode = document.getElementById('root');

if (!rootNode) throw 'Element "root" not found';

createRoot(rootNode).render(
  <Provider store={store}>
    <FirebaseAppProvider apiKey={firebaseApiKey} projectId={firebaseProjectId}>
      <AuthProvider>
        <HotkeysProvider>
          <BrowserRouter basename={baseUri.pathname}>
            <Routes />
          </BrowserRouter>
        </HotkeysProvider>
      </AuthProvider>
    </FirebaseAppProvider>
  </Provider>
);
