import { ReactElement, useState } from 'react';

import { Button, Card, Elevation, Spinner, SpinnerSize } from '@blueprintjs/core';
import { signInWithPopup, getAuth, GoogleAuthProvider } from 'firebase/auth';

import { AuthState, useAuth, useFirebase } from './AuthenticationHooks';

export default function LoginCard(): ReactElement {
  const firebase = useFirebase();

  const { authState, signIn } = useAuth();
  const [failureMessage, setFailureMessage] = useState('');

  function onError() {
    setFailureMessage('Login Failed');
  }

  function startSignIn() {
    signInWithPopup(getAuth(firebase.app), new GoogleAuthProvider())
      .then((result) => {
        console.log('Sign In Success', result);
        if (signIn) {
          signIn({
            displayName: result.user.displayName || '',
            email: result.user.email || '',
            profileImageUrl: result.user.photoURL || ''
          });
        }
      })
      .catch((result) => {
        console.log('Sign In Failed', result);
        onError();
      });
  }

  return (
    <div className="loginCardContainer">
      {authState === AuthState.INITIALIZING ? (
        <Spinner intent="primary" size={SpinnerSize.STANDARD} />
      ) : (
        <Card elevation={Elevation.THREE}>
          <h2>Sign In</h2>
          <p>Authentication is required to use this application.</p>

          {failureMessage && <p style={{ color: 'red' }}>{failureMessage}</p>}

          <div className="buttonContainer">
            <Button onClick={startSignIn}>Sign in with Google</Button>
          </div>
        </Card>
      )}
    </div>
  );
}
