import { ReactElement } from 'react';
import { useAppSelector } from '../../app/Hooks';

import { Card, Elevation } from '@blueprintjs/core';

export default function CreditScores(): ReactElement {
  const scores = useAppSelector((state) => state.creditScores.scores);
  const sortedScores = Array.from(scores)
    .map((entry) => ({ userId: entry[0], creditScoreReport: entry[1] }))
    .sort((user1, user2) => user1.userId - user2.userId);

  // TODO: this displays '0:'
  return (
    <Card elevation={Elevation.TWO}>
      <h1>Credit Scores</h1>
      {sortedScores.map((score) => (
        <p key={score.userId}>
          {score.userId}: {score.creditScoreReport.score} (
          {score.creditScoreReport.timestamp.toDateString()})
        </p>
      ))}
    </Card>
  );
}
