import { ReactElement } from 'react';
import { Routes, Route, Outlet } from 'react-router-dom';

import { Tab } from '@blueprintjs/core';

import NavigatingTabs from '../components/NavigatingTabs';

import Accounts from './Accounts';
import NotFound from './NotFound';

export default function Data(): ReactElement {
  return (
    <Routes>
      <Route path="/" element={<Layout />}>
        <Route path="accounts" element={<Accounts />} />
        <Route path="income" element={<div>TBD - Income</div>} />
        <Route path="recurring-payments" element={<div>TBD - Recurring</div>} />
        <Route path="investments" element={<div>TBD - Investments</div>} />
        <Route path="assets" element={<div>TBD - Assets</div>} />
        <Route path="*" element={<NotFound />} />
      </Route>
    </Routes>
  );
}

function Layout() {
  return (
    <NavigatingTabs name="Data" defaultTab="accounts">
      <Tab id="accounts" title="Accounts" panel={<Outlet />} />
      <Tab id="income" title="Income" panel={<Outlet />} />
      <Tab id="recurring-payments" title="Recurring Payments" panel={<Outlet />} />
      <Tab id="investments" title="Investments" panel={<Outlet />} />
      <Tab id="assets" title="Assets" panel={<Outlet />} />
    </NavigatingTabs>
  );
}
