import { createSlice, PayloadAction } from '@reduxjs/toolkit';

export enum UserId {
  Mark,
  Ivy
}

export interface CreditScoreReport {
  timestamp: Date;
  score: number;
  source: string;
}

export interface CreditScoreReportUpdate {
  userId: UserId;
  newScore: number;
  source: string;
}

export interface CreditScoresState {
  scores: Map<UserId, CreditScoreReport>;
}

// TODO: Temporary data for testing
const initialState: CreditScoresState = {
  scores: new Map<UserId, CreditScoreReport>([
    [UserId.Mark, { timestamp: new Date(), score: 100, source: 'TransUnion' }],
    [UserId.Ivy, { timestamp: new Date(), score: 200, source: 'Experian' }]
  ])
};

export const creditScoresSlice = createSlice({
  name: 'creditScores',
  initialState,
  reducers: {
    updateScore: (state, action: PayloadAction<CreditScoreReportUpdate>) => {
      state.scores.set(action.payload.userId, {
        timestamp: new Date(),
        score: action.payload.newScore,
        source: action.payload.source
      });
    }
  }
});

export default creditScoresSlice.reducer;
export const { updateScore } = creditScoresSlice.actions;
